/*General*/
.App {
  min-width: 350px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  padding-bottom: 20px;
}
.MainView{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;

    width: 100%;
    height: 100%;
}
.important{
    font-size: 3vh;
    color: black;
}
.headline_{
    display: flex;
    font-weight: bold;
    margin: 0;
    align-items: center;
    color: black;
}
.button{
    flex: 1 1;
    margin: 5px;
    background: #466496;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-width: 30%;
    outline: none;
    border: 1px solid aliceblue;
    border-radius: 10px;
    color: white;
}
.button:disabled{
    background: #5C6E8E;
}

/* Control Panel Stuff */
.ControlPanel{
  display: flex;
  flex: 1 1;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  height: 10vh;
  width: 100%;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid grey;
}
.ControlPanelChild{
    height: 8vh;
    flex: 1 1;
    display: flex;
    justify-content: center;
    flex-direction: row;
}
.counterContainer{
    background: #809fba;
    color: black;
    padding: 5px;
    width: 95%;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 20px;
}

/*Scoreboard stuff*/
.Scoreboard{
    font-size: 2.5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    min-width: 25vw;
    height: 50vh;
    padding: 5px;

    overflow: scroll -moz-scrollbars-none;
}
.Scoreboard tbody, thead{
    display: block;
}
.Scoreboard tr{
    display: flex;
    justify-content: space-around;
}
.grommetux-table-row{
    width: 100%;
    margin-bottom: 10px;
}
.Scoreboard tbody{
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 65vh;
    text-align: center;
}


/*Playground stuff */
#Field hr{
    margin: -5px;
    border: 0;
}
.activeTile{
    background: #EFDA51 !important;
}
.Tile {
  height: 10vw;
  max-height: 12vh;
  width: 10vw;
  max-width: 12vh;
  display: inline-block;
  margin: 0;
  padding: 0;
  border: 1px solid black;
  background: #466496;
  outline: none;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: background 0.2s ease-in-out, -webkit-transform 0.6s linear;
  transition: transform 0.6s linear, background 0.2s ease-in-out;
  transition: transform 0.6s linear, background 0.2s ease-in-out, -webkit-transform 0.6s linear;
}

/*Settings for mobile*/
@media (max-width: 600px){
    .MainView{
        flex-direction: column;
        align-items: center;
    }
    .Scoreboard{
        min-width: 250px;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .App{
        padding: 0;
        margin: 0;
    }
    .Tile{
        width: 15vw;
        height: 15vw;
    }
}
.popup{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    background: white;
    opacity: 0;

    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;

    height: 0;
    width: 0;
    max-width: 90vw;

    border: 2px solid black;
    border-radius: 10px;

    transition: all 0.2s ease-in-out;
}

.popup_visible{
    height: 250px;
    width: 400px;
    opacity: 1;
}
